/// @file SpriteBitmap.js
///

(function () {

  Sprite.Bitmap = class Bitmap extends Sprite.Display {

    constructor (image) {
      super();
      this._image = image;
      this._width = image.width;
      this._height = image.height;
    }

    get width () {
      return this._width;
    }

    set width (value) {
      this._width = value;
    }

    get height () {
      return this._height;
    }

    set height (value) {
      this._height = value;
    }

    draw (context) {
      this.drawImage(this._image, 0, 0, this._width, this._height);
    }

  };

})();
